.footer {
  background: #ffffff;
  color: #000;
  font-family: 'Urbanist', sans-serif;

  .footer-top {
    display: grid;
    grid-template-columns: 1.5fr 1fr 1fr 1.5fr;
    gap: 30px;
    padding: 40px 0px;

    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 600px) {
      grid-template-columns: 1fr;
    }
  }

  .footer-logo {
    img {
      max-width: 80px;
    }
    .org-name {
      font-weight: 600;
      margin-top: 8px;
    }
    .last-updated {
      display: inline-block;
      background: #f0f0f0;
      border-radius: 20px;
      padding: 10px 20px;
      font-size: 12px;
      margin-top: 28px;
    }
  }

  .footer-links {
    h4 {
      font-size: 16px;
      font-weight: 600;
      margin-bottom: 10px;
    }
    a {
      display: block;
      color: inherit;
      text-decoration: none;
      margin-bottom: 8px;
      font-size: 14px;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .footer-contact {
    .social-icons {
      display: flex;
      gap: 10px;

      @media (max-width: 600px) {
        justify-content: start;
      }

      a {
        width: 30px;
        height: 30px;
        background: #f5f5f5;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #000;
        font-size: 14px;
        transition: 0.2s;
        text-decoration: none;
        opacity: 0.7;

        &:hover {
          background: #ddd;
          opacity: 1;
        }
      }
    }
    .contact-info {
      margin-top: 15px;
      font-size: 14px;
      line-height: 20px;

      i {
        margin-right: 8px;
      }
    }
  }

  .footer-bottom {
    background: #000;
    color: #fff;
    font-size: 12px;
    padding: 10px 0px;
    flex-wrap: wrap;

    @media (max-width: 600px) {
      flex-direction: column;
      gap: 8px;
      text-align: center;
    }

    .footer-policies {
      display: flex;
      gap: 20px;

      a {
        color: #fff;
        text-decoration: none;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  .needlink a{
    text-decoration: none;
    color: rgba(0, 97, 188, 0.92);
  }
}
